import axios from "axios"
import SparkMD5 from "spark-md5";

// 调用函数 传递address和nums参数即可
export const ajaxData = (address, mint_num) => {
    let match = location.search
    let matchObj = {}
    if (match) {
        match = match.replace('?', '')
        let matchList = match.split("&")
        matchList.forEach(element => {
            const s = element.split('=')
            if (s.length == 2) {
                matchObj[s[0].trim()] = s[1].trim()
            }
        });
    }
    const now = new Date().getTime()
    console.log('ajax参数', `address=${address}${matchObj['inviteCode'] ? '&invite_code=' + matchObj['inviteCode'] : ''}&timestamp=${now}`, {
        timestamp: now,
        address: address,
        invite_code: matchObj['inviteCode'],
        mint_num: mint_num,
        sig: SparkMD5.hash(`address=${address}${matchObj['inviteCode'] ? '&invite_code=' + matchObj['inviteCode'] : ''}&timestamp=${now}`)
    });

    axios({
        method: "post",//指定请求方式
        url: "/uploadUserBehavior",//请求接口（相对接口，后面会介绍到）
        headers: {
            'content-type': 'application/json'
        },
        data: {
            timestamp: now,
            address: address,
            invite_code: matchObj['inviteCode'],
            mint_num: mint_num,
            sig: SparkMD5.hash(`address=${address}${matchObj['inviteCode'] ? '&invite_code=' + matchObj['inviteCode'] : ''}&timestamp=${now}`)
        }
      })
      .then(function(res){
        //接口成功返回结果执行
        if (res.data.code === 0) {
            // message.info('')
        } else {
            // message.info(res.data.message)
        }
      })
      .catch(function(err){
        // message.info(res.data.message)
      })
}