                  desc: 'Having a higher Passion Attribute will result in better WC earnings per Electricity spent'
              },
              {
                  title: 'Maneuverability',
                  desc: 'The higher the maneuverability, the more FC earned per minute.'
              },
              {
                  title: 'Electric Energy',
                  desc: 'Power consumption affects the decay rate of elctric energy. Lower Power consumption will result in a slower electric energy decay.'
              },
              {
                  title: 'Mystery Box',
