                      details: '我是详情',
                      isShowWarp: false,
                  }
              ]
          },
          footerData: [
              {
                  img: require('@/assets/icons/photo.png'),
                  name: '',
                  url: '',
              },
              {
                  img: require('@/assets/icons/f.png'),
                  name: '',
                  url: '',
              },
              {
                  img: require('@/assets/icons/tw.png'),
                  name: '',
                  url: '',
              },
              {
                  img: require('@/assets/icons/in.png'),
                  name: '',
                  url: '',
              },
              {
                  img: require('@/assets/icons/video.png'),
                  name: '',
                  url: '',
              },
              {
                  img: require('@/assets/icons/music.png'),
                  name: '',
                  url: '',
              }

          ],
