            container : '#banner1',//选择容器 必选
            imgs : [
                require('@/assets/imgs/i1.png'),
                require('@/assets/imgs/i2.png')
                ,require('@/assets/imgs/i3.png')
                ,require('@/assets/imgs/i4.png')
