      },
      targgleWrap(type, index) {
          const target = this.peopleData[type][index];
          target.isShowWarp = !target.isShowWarp
          this.peopleData[type][index].splice(index, target, 1)
      },
      isOverToday(dateStr) {
          console.log(dateStr, new Date().getTime() >= new Date(dateStr).getTime());
          return new Date().getTime() >= new Date(dateStr).getTime()
      },
        closeModal(e) {
            console.log(e);
            if (e.target.className === "modal-warp") {
                this.isShowModal = false;
            }
        },
        showModal(e) {
            this.isShowModal = true;
        }
  },
}
