<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
$back-color: #000;
$noraml-size: 24px;
$desc-title-size: 24px;
$desc-desc-size: 16px;
$default-row-padding: 120px;
$default-distant: 15px;
$title-top-padding: 45px;
$title-top-size: 48px;
$sm-title-top-size: 32px;
$header-nav-a-size: 16px;

$logo-text-color: #00e5ff;

$button-color: #323232;
$button-back-color: #7800fd;
$purple-color: #fe00ff;

$f-size-1: 70px;
$f-size-2: 60px;
$f-size-3: 17px;
$f-size-4: 36px;

$people-icon-size: 240px;

$header-height: 70px;

.desc-text-base {
    font-size: $desc-desc-size;
    line-height: 2em;
    font-weight: 100;
}

.logo-text-color {
    color: $logo-text-color;
}

.default-padding {
    box-sizing: border-box;
    padding: 0 $default-row-padding;
}

.back-color {
    background-color: $back-color;
    color: #fff;
    font-size: $noraml-size;
}

.flex-no-warp {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}

#app {
    background-color: #000;
}

.sp-text {
    color: $logo-text-color;
}

.full {
    width: 100%;
    height: 100%;
}

.clear-font-weight {
    font-weight: 100;
}

.purpleColor {
    color: $purple-color !important;
}

@keyframes mymove
    {
    from {transform: scale(4.5);}
    to {transform: scale(1);}
}

.motor {
    @extend .full;
    header {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        height: $header-height;
        @extend .back-color;
        z-index: 100;
        .motor-header {
            height: 100%;
            @extend .flex-no-warp;
            @extend .default-padding;
            // padding: 0 10%;
            .motor-header__logo {
                height: 100%;
                @extend .flex-no-warp;
                font-size: 20px;
