            height: 5px;
            flex-basis: 50%;
            background-color: rgba(0, 229, 255, 1);
        }
        .line-bottom {
            width: 100%;
            height: 10px;
            flex-basis:10px;
            background-image: linear-gradient(45deg,transparent 10px,rgba(0, 229, 255, 1) 10px),linear-gradient(-45deg,transparent 10px,rgba(0, 229, 255, 1) 10px);
