        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.6);
        z-index: 9999;
    }

    .modal-view {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: #fff;
        width: 320px;
        border-radius: 10px;
        padding: 30px;
    }
    .modal-item {
        border-bottom: 1px solid #e2e2e2;
        padding: 10px 0 10px 0;
    }

    .modal-item:first-child {
        padding-top: 0;
    }

    .modal-item:last-child {
        border-bottom: none;
    }

    .modal-item__img {
        width: 100%;
        text-align: center;
    }

    .modal-item__img img {
        width: 40px;
        height: 40px;
    }

    .modal-item__baseTitle {
        width: 100%;
        text-align: center;
    }

    .modal-item__title {
        font-size: 16px;
        color: #333;
        margin-top: 10px;
        font-weight: bold;
    }

    .modal-item__subtitle {
        font-size: 14px;
        color: #999;
        margin-top: 10px;
    }
}

